import React from 'react';
import { useTranslation } from 'react-i18next';

interface RTLProviderProps {
  children: React.ReactNode;
}

export default function RTLProvider({ children }: RTLProviderProps) {
  const { i18n } = useTranslation();

  React.useEffect(() => {
    // Handle RTL/LTR direction
    document.dir = i18n.language === 'ar' ? 'rtl' : 'ltr';
    document.documentElement.lang = i18n.language;
    
    // Add language-specific classes for styling
    document.documentElement.classList.remove('lang-ar', 'lang-en', 'lang-tr');
    document.documentElement.classList.add(`lang-${i18n.language}`);

    // Add RTL-specific font for Arabic
    document.documentElement.style.fontFamily = 
      i18n.language === 'ar' ? 'Noto Sans Arabic, sans-serif' : 'Inter var, sans-serif';
  }, [i18n.language]);

  return (
    <div className={`${i18n.language === 'ar' ? 'font-arabic' : 'font-sans'} min-h-screen`}>
      {children}
    </div>
  );
}